import React, { Component } from 'react';
var paper = require('paper');
var controlPoint = require('./controlPoint');

class SketchFace extends Component {

    state = {
        autoAdjust: true,
        selectedPoint: null,
    };


    constructor(props) {
        super(props);

        this.face = {
            path: [],
            iterations: [3, 2],
            variation: [5, 10],
            isOneShape: [true, true],
            isFilled: [false, false],
            forehead: new controlPoint.create(this, 'face' , 'forehead', .275, .239, 1, false),
            cheek: new controlPoint.create(this, 'face' , 'cheek', .31, .475, 1, false),
            cheekShadowTop: new controlPoint.create(this, 'face' , 'cheekShadowTop', .275, .506, .5, false),
            cheekShadowMiddle: new controlPoint.create(this, 'face' , 'cheekShadowMiddle', .187, .68, .5, false),
            cheekShadowBottom: new controlPoint.create(this, 'face' , 'cheekShadowBottom', .156, .845, .75, false),
            shadowEye: new controlPoint.create(this, 'face' , 'shadowEye', .253, .436, .75, false),
            cheekConcave: new controlPoint.create(this, 'face' , 'cheekConcave', .295, .595, .435),
            jaw: new controlPoint.create(this, 'face' , 'jaw', .255, .76, .67),
            chinShadow: new controlPoint.create(this, 'face' , 'chinShadow', 0, .923, .5, false, true),
            chin: new controlPoint.create(this, 'face' , 'chin', .08, .935, .417),
            isClosed: [false, false],
        };
        this.face.order = [
            [this.face.cheek, this.face.cheekConcave, this.face.jaw, this.face.chin],
            [this.face.shadowEye, this.face.cheekShadowTop, this.face.cheekShadowMiddle, this.face.cheekShadowBottom, this.face.chin, this.face.chinShadow]
        ];
        
        this.mouth = {
            path: [],
            iterations: [3, 3],
            variation: [5, 5],
            isOneShape: [true, true],
            isFilled: [false, false],
            lip: new controlPoint.create(this, 'mouth' , 'lip', .12, .76, 0),
            lowerLip: new controlPoint.create(this, 'mouth' , 'lowerLip', .055, .8, 1),
            upperLip: new controlPoint.create(this, 'mouth' , 'upperLip', .045, .715, .405),
            cupidsBow: new controlPoint.create(this, 'mouth' , 'cupidsBow', 0, .725, .425, false, true),
            mouthLineCenter: new controlPoint.create(this, 'mouth', 'mouthCenter', 0, .752, .5, false, true),
            mouthLineTips: new controlPoint.create(this, 'mouth', 'mouthCenter', .061, .745, .5),
            isClosed: [true, false],
        }
        this.mouth.order = [
            [this.mouth.cupidsBow, this.mouth.upperLip, this.mouth.lip, this.mouth.lowerLip],
            [this.mouth.lip, this.mouth.mouthLineTips, this.mouth.mouthLineCenter]
        ];

        this.nose = {
            path: [],
            iterations: [3, 2],
            variation: [3, 3],
            isOneShape: [true, false],
            isFilled: [false, false],
            top: new controlPoint.create(this, 'nose', 'top', .066, .59, 0),
            middle: new controlPoint.create(this, 'nose', 'middle', .074, .632, 1),
            bottom: new controlPoint.create(this, 'nose', 'bottom', .035, .65, 1),
            nostrilStart: new controlPoint.create(this, 'nose', 'nostrilStart', .052, .633, 1),
            nostrilMiddle: new controlPoint.create(this, 'nose', 'nostrilMiddle', .036, .632, 1),
            nostrilEnd: new controlPoint.create(this, 'nose', 'nostrilEnd', .015, .6532, 1),
            noseTip: new controlPoint.create(this, 'nose', 'noseTip', 0, .655, .5, false, true),
            isClosed: [false, true],
        }
        this.nose.order = [
            [this.nose.top, this.nose.middle, this.nose.bottom, this.nose.nostrilStart, this.nose.nostrilMiddle, this.nose.nostrilEnd, this.nose.noseTip],
            [this.nose.nostrilEnd, this.nose.nostrilMiddle, this.nose.nostrilStart],
        ];

        this.eyes = {
            path: [],
            iterations: [3, 2, 3, 6],
            variation: [3, 3, 3, 4],
            isFilled: [false, false, false, false],
            isOneShape: [false, false, false, false],
            innerCorner: new controlPoint.create(this, 'eyes', 'innerCorner', .076, .457, .5),
            outerCorner: new controlPoint.create(this, 'eyes', 'outerCorner', .205, .446, .25),
            innerTop: new controlPoint.create(this, 'eyes', 'innerTop', .107, .431, 1),
            outerTop: new controlPoint.create(this, 'eyes', 'outerTop', .186, .425, 1),
            middleTop: new controlPoint.create(this, 'eyes', 'middleTop', .148, .418, 1),
            innerBottom: new controlPoint.create(this, 'eyes', 'innerBottom', .113, .460, 1),
            outerBottom: new controlPoint.create(this, 'eyes', 'outerBottom', .18, .459, 1),

            innerHoodCorner: new controlPoint.create(this, 'eyes', 'innerHoodCorner', .081, .436, 1),
            innerHoodTop:new controlPoint.create(this, 'eyes', 'innerHoodCorner', .116, .406, 1),
            outerHoodTop:new controlPoint.create(this, 'eyes', 'innerHoodCorner', .19, .403, 1),
            outerHoodCorner: new controlPoint.create(this, 'eyes', 'innerHoodCorner', .232, .43, 1),

            isClosed: [true, false, false, true],
        }

        this.eyes.eyeCenter = () => { return {
            x: () => this.eyes.middleTop.point().left.x,
            y: () => this.eyes.outerCorner.point().left.y + (this.eyes.middleTop.point().left.y - this.eyes.outerCorner.point().left.y) / 2,
            r: () => (this.eyes.innerCorner.point().left.x - this.eyes.outerCorner.point().left.x) / 5,
        }};

        this.eyes.corneaPoints = () => {
            let circle = new paper.Path.Circle(new paper.Point(this.eyes.eyeCenter().x(), this.eyes.eyeCenter().y()), this.eyes.eyeCenter().r());
            let intersection = circle.intersect(this.eyes.path[0]);
            intersection.divideAt(intersection.length * .5847);
            intersection.divideAt(intersection.length * .87719);
            let temp = intersection.segments.map(i => i.point);
            temp = temp.map (i => controlPoint.createMirrorPoints(i, 1));
            temp.push(temp.shift());  
            temp.push(temp.shift());
            temp.shift();
            circle.remove();
            intersection.remove();
            return temp;
        }

        this.eyes.pupilPoints = () => {
            let circle = new paper.Path.Circle(new paper.Point(this.eyes.eyeCenter().x(), this.eyes.eyeCenter().y()), this.eyes.eyeCenter().r()  / 2.5);
            let temp = circle.segments.map(i => i.point);
            temp = temp.map(i => controlPoint.createMirrorPoints(i, 1) );
            circle.remove();
            return temp;
        }

        this.eyes.centerLinePoints = () => {
            let path = this.eyes.path[this.eyes.path.length - 1];
            let points = path.segments.map(i => i.point);
            let temp = [points[0], points[1], points[points.length - 2], points[points.length - 1]];
            temp = temp.map(i => controlPoint.createMirrorPoints(i, 1));
            return temp;
        }
        
        this.eyes.order = [
            [this.eyes.innerCorner, this.eyes.innerTop, this.eyes.middleTop, this.eyes.outerTop, this.eyes.outerCorner, this.eyes.outerBottom, this.eyes.innerBottom],
            [this.eyes.innerHoodCorner, this.eyes.innerHoodTop, this.eyes.outerHoodTop, this.eyes.outerHoodCorner],
            () => {return this.eyes.corneaPoints()},
            () => {return this.eyes.pupilPoints()}
        ]

        this.eyes.order.push([])

        this.points = [
            // face
            this.face.cheek, this.face.cheekConcave, this.face.jaw, this.face.chin,
            this.face.shadowEye, this.face.cheekShadowTop, this.face.cheekShadowMiddle, this.face.cheekShadowBottom, this.face.chin, this.face.chinShadow,

            // mouth
            this.mouth.cupidsBow, this.mouth.upperLip, this.mouth.lip, this.mouth.lowerLip, this.mouth.mouthLineTips, this.mouth.mouthLineCenter,

            // nose
            this.nose.top, this.nose.middle, this.nose.bottom, this.nose.nostrilStart, this.nose.nostrilMiddle, this.nose.nostrilEnd, this.nose.noseTip,

            // eyes
            this.eyes.innerCorner, this.eyes.outerCorner, this.eyes.innerTop, this.eyes.outerTop, this.eyes.outerBottom, this.eyes.innerBottom, this.eyes.middleTop, this.eyes.innerHoodCorner, this.eyes.innerHoodTop, this.eyes.outerHoodTop, this.eyes.outerHoodCorner
        ]; 


        paper.view.onMouseEnter = (event) => {
            this.points.forEach(point => {
                point.showHighlight();
            });
            if (this.state.selectedPoint) {
                this.state.selectedPoint.highlight();            
            }
        }

        paper.view.onMouseLeave = (event) => {
            this.points.forEach(point => {
                point.clearHighlight();
            });
        }

        paper.view.onKeyDown = (event) => {
            if (this.state.selectedPoint) {
                switch(event.key) {
                    case 'right':
                        this.state.selectedPoint.point(1, 0);
                        break;
                    case 'left':
                        this.state.selectedPoint.point(-1, 0);
                        break;
                    case 'up':
                        this.state.selectedPoint.point(0, -1);
                        break;
                    case 'down':
                        this.state.selectedPoint.point(0, 1);                
                        break;
                    default:
                }
            }
        }

        paper.view.onClick = (event) => {
            console.log("x: " + (paper.view.size.width / 2 - event.point.x)/paper.view.size.width + " & y: " + event.point.y/paper.view.size.width);
        }
    }


    componentDidMount() {
        this.draw('face');
        this.draw('mouth');
        this.draw('nose');
        this.draw('eyes');

        var rect = new paper.Path.Rectangle({
            point: [0, 0],
            size: [paper.view.size.width, paper.view.size.height],
        });
        rect.sendToBack();
        // rect.fillColor = '#e5e5e5';
    }


    

    /**
     * Draw parts of the face onto the canvas.
     * @param {string} region - facial region to redraw
     * @return {null}
     */

    draw = (region) => {
        // clear the drawing + empty the paths database
        this[region].path.forEach(i => i.remove());
        this[region].path = [];
        if (this[region].path2) {
            this[region].path2.forEach(i => i.remove());
            this[region].path2 = [];
        }
        
        // if draws only one shape
        for (let i = 0; i < this[region].order.length; i++) {
            let orderRef = typeof(this[region].order[i]) === 'function' ? this[region].order[i]() : this[region].order[i];

            if (this[region].isOneShape[i]) {
                let reverse = orderRef.reverse().filter(i => i.point().right);
                let curves = [...orderRef.reverse().map(i => i.curve()), ...reverse.map(i => i.curve())];
                let order = [...orderRef.map(i => i.point().left), ...reverse.map(i => i.point().right)];

                let temp = this.makePath(
                    order,
                    curves,
                    this[region].variation[i],
                    this[region].isClosed[i],
                    this[region].iterations[i],
                    this[region].isFilled[i]
                );
    
                // add to paths database
                this[region].path.push(...temp);
            } else { // draws two separate shapes
                let curves = orderRef.map(i => i.curve());
                let orderLeft = orderRef.map(i => i.point().left);
                let orderRight = orderRef.map(i => i.point().right);
    
                let tempLeft = this.makePath(
                    orderLeft,
                    curves,
                    this[region].variation[i],
                    this[region].isClosed[i],
                    this[region].iterations[i],
                    this[region].isFilled[i]
                );
    
                let tempRight = this.makePath(
                    orderRight,
                    curves,
                    this[region].variation[i],
                    this[region].isClosed[i],
                    this[region].iterations[i],
                    this[region].isFilled[i]
                );
    
                // add to paths database
                this[region].path.push(...tempLeft, ...tempRight);
            }
        }
    }

    /**
     * Makes the path object
     * @param {array} order - order of points to add
     * @param {array} curves - curves in relation the the points
     * @param {number} variation - variation that the points should go under
     * @param {boolean} closed - whether or not the path should be closed
     * @param {number} iterations - total iterations
     * @param {boolean} isFilled - whether or not to fill in first path of shape
     * @returns {array} list of paths created
     */

     makePath(order, curves, variation, closed, iterations, isFilled){
         let paths = [];

        for (let i = 0; i < iterations; i++) {
            let temp = new paper.Path();
            temp.closed = closed;
            temp.strokeColor = 'black';
            temp.strokeWidth = .6 * (i + 1) / iterations;
    
            // draw left + right side
            order.forEach(i => {
                const randSignX = Math.random() < .5 ? 1 : -1;
                const randSignY = Math.random() < .5 ? 1 : -1;
                temp.add(i.add(new paper.Point(Math.random() * variation * randSignX, Math.random() * variation * randSignY)))
            });
            
            // adjust curves left + right side;
            temp.smooth({type: 'geometric'});
            for (let i = 0; i < curves.length; i++) {
                temp.segments[i].handleIn = temp.segments[i].handleIn.multiply(curves[i]);
                temp.segments[i].handleOut = temp.segments[i].handleOut.multiply(curves[i]);
            }

            paths.push(temp);
        }

        if (isFilled) paths[0].fillColor = 'black';

        return paths;
     }


     /**
      * Control curve change.
      * @param {object} event
      * @return {null}
      */
     handleCurveChange = (event) => {
        let v = event.target.value;
        this.state.selectedPoint.curve(v);
        this.setState({selectedPoint: this.state.selectedPoint});
     }


    render() {
        if (!this.state.selectedPoint) {
            return ('this is null');
        } else {
            return (
                <div id="controls">
                    <label htmlFor='selected-point'>Curvature</label>
                    <input
                    type='range'
                    step='.005'
                    min='0'
                    max='1'
                    id='selected-point'
                    value={this.state.selectedPoint.curve()}
                    onChange={this.handleCurveChange}
                    />
                </div>
            )
        }
    }
}

export default SketchFace;


